export interface OWspsMgrInit extends OSkinableInit {
	reg: IReg<IChainEnv>;
	tabAreasLists?: string
}

export class WspsMgr<E extends IRegPointer<IChainEnv>> extends BaseAreaViewAsync<E> implements IViewsContainer {
	reg: IReg<IChainEnv>;
	_tabs: Tabs;
	_msg: MsgLabel;

	protected async _initialize(init: OWspsMgrInit) {
		this.reg = init.reg;
		this._msg = <MsgLabel/> as MsgLabel;
		this.reg.addToList("wsps-mgr-tab", "alive", 1, new AreaAsync<OWspsListMgrInit, WspsListMgr<E>>()
			.setLabel("Ateliers")
			.setBodyBuilder((ctx) => {
				return <AliveWspsMgr î={{reg: this.reg} as OWspsListMgrInit}/> as AliveWspsMgr<E>;
			}), 1);
		if (this.reg.env.universe.config.backEnd === "odb") {
			this.reg.addToList("wsps-mgr-tab", "trashed", 1, new AreaAsync<OWspsListMgrInit, WspsListMgr<E>>()
				.setLabel("Corbeille")
				.setDescription("Ateliers supprimés placés en corbeille")
				.setBodyBuilder((ctx) => {
					return <TrashedWspsMgr î={{reg: this.reg} as OWspsListMgrInit}/> as TrashedWspsMgr<E>;
				}), 1);
		}

		this._tabs = <Tabs areas-context="wsps-mgr-tab-ctxt" areas-list={init.tabAreasLists || "wsps-mgr-tab"}/> as Tabs;

		this._attach(this.tagName, init, this._tabs);
	}

	onViewBeforeHide(close?: boolean): boolean {
		return this._tabs.selectedTab.view.onViewBeforeHide(close);
	}

	async onViewWaitForHide(close?: boolean): Promise<boolean> {
		return this._tabs.selectedTab.view.onViewWaitForHide(close);
	}

	visitViews(visitor: (view: IView) => any, options?: OViewVisitOptions): any {
		if (this._tabs) this._tabs.visitViews(visitor, options);
	}

	visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		if (this._tabs)
			return this._tabs.visitViewsAsync(visitor, options);
	}

}

REG.reg.registerSkin('wsps-wspsmgr', 1, /* language=CSS */ `
	:host {
		display: flex;
		flex: 1;
		min-width: 0;
		min-height: 0;
	}

	c-tabs {
		flex: 1;
	}
`);

customElements.define("wsps-wspsmgr", WspsMgr);


export interface OWspsListGridInit extends OGridSmallInit {
	/** Actions associées aux items sélectionnés (menu contextuel et IFocusActionables). */
	actions?: IAction<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>[]

	/** Racourcis claviers à capter sur la sel courante. */
	accelKeyMgr?: AccelKeyMgr<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>

	/** Supprime le lineDrawer par défaut (avec null) ou customise le lineDrawer. */
	lineDrawer?: null | IGridLineDrawer

}

export interface OWspsListMgrInit extends OSkinableInit {
	reg: IReg<IChainEnv>
	/** Init de la liste des ateliers. */
	wspsGrid?: OWspsListGridInit
}

abstract class WspsListMgr<E extends IRegPointer<IChainEnv>> extends BaseAreaViewAsync<E> implements IGridLineDrawer {

	reg: IReg<IChainEnv>;

	protected wspsToolbar: BarActions<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>;

	protected wspsActions: IAction<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>[];

	protected wspsActionsContext: (IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>) & ICreateWspCtx = {
		openWspAfterCreate: false,
	};

	protected filterInput: HTMLInputElement;

	protected grid: GridSmall & IContextMenuActionsPointer<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>;

	protected _lastListWsps: (JWspInfoInList & IWspPointer)[];

	protected dataHolder: GridDataHolderJsonTree<JWspInfoInList & IWspPointer>;

	protected fetchState: 'pending' | 'done' | 'failed';

	protected async _initialize(init: OWspsListMgrInit): Promise<void> {
		this.reg = REG.createSubReg(init.reg);
		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin("wsps-list-wspsmgr", init);
	}

	protected async initUi(init: OWspsListMgrInit): Promise<void> {
		const sr = this.shadowRoot;

		this.grid = new GridSmall();

		this._onWspLiveStateChange = this.onWspLiveStateChange.bind(this);
		// Pour suivi des ateliers **chargés** uniquement
		(this.reg.env as IWspsLivePlacePointer).place = this.reg.env.universe.wspServer.wspsLive.newPlace();
		(this.reg.env as IWspsLivePlacePointer).place.eventsMgr.on("wspLiveStateChange", this._onWspLiveStateChange);

		/** Init les listes du registre **/
		this.reg.addToList("actions:wsps:wspsmgr:toolbar", "actionRefreshUi", 1, actionRefreshUi);

		/** Accelkey **/
		let accelKeyMgr = new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:wsps:wspsmgr"));

		/** Toolbar **/
		this.wspsActions = this.reg.mergeLists("actions:wsps:wspsmgr:toolbar", "actions:wsps:wspsmgr");
		accelKeyMgr.initFromMapActions(this.reg.getListAsMap("accelkeys:wspApp:global"));

		let headerElt = sr.appendChild(<div id="header"/>);
		this.filterInput = headerElt.appendChild(<div id="search"/>).appendChild(<input placeholder="Filtrer..." type="search" oninput={this.onFilterInput}/>) as HTMLInputElement;
		this.wspsToolbar = headerElt.appendChild(new BarActions<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>().initialize({
			reg: this.reg,
			actions: ACTION.injectSepByGroup(this.wspsActions, this.reg.getPref("actions.wsps.wspsmgr.groupOrder", "wsps * refresh"), this.wspsActionsContext),
			uiContext: "bar",
			actionContext: this.wspsActionsContext,
			disableFullOverlay: true,
		} as OBarActionsInit<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>));

		/** Grid **/
		this.dataHolder = new GridDataHolderJsonTree<JWspInfoInList>('wsps');

		const initPacksGrid: OWspsListGridInit = BASIS.newInit(init.wspsGrid, this.reg);
		if (initPacksGrid.skinScroll === undefined) initPacksGrid.skinScroll = 'scroll/small';
		if (initPacksGrid.lineDrawer === undefined) initPacksGrid.lineDrawer = this;
		if (initPacksGrid.accelKeyMgr === undefined) initPacksGrid.accelKeyMgr = accelKeyMgr;
		initPacksGrid.dataHolder = this.dataHolder;
		initPacksGrid.selType = "multi";
		initPacksGrid.emptyBody = this.emptyBody.bind(this);
		initPacksGrid.skinOver = "wspsmgr-list-grid";
		initPacksGrid.autoSelOnFocus = 'first';
		initPacksGrid.defaultAction = OpenPropsWspAction.SINGLETON;

		this.grid.initialize(initPacksGrid);

		this.grid.ctxMenuActions = {
			actions: ACTION.injectSepByGroup(this.reg.mergeLists("actions:wsps:wspsmgr:ctxtmenu", "actions:wsps:wspsmgr") as IAction<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>[], this.reg.getPref("actions.wsps.wspsmgr.groupOrder", "wsps wps * refresh"), this) as IAction<IWspsActionCtx | IWspActionCtx | IRegPointer<IChainEnv>>[],
			actionContext: this.wspsActionsContext,
		};

		this.grid.addEventListener('keydown', (ev: KeyboardEvent) => {
			accelKeyMgr.handleKeyboardEvent(ev, this);
		});

		this.grid.addEventListener('grid-select', function (this: GridSmall, ev: Event) {
			const wspsMgr = DOMSH.findHost<WspsListMgr<any>>(this);
			// Refresh de l'actionContext des actions
			let selectedWsps = wspsMgr.getSelectedWsps();
			(wspsMgr.wspsActionsContext as IWspActionCtx).reg = (wspsMgr.wspsActionsContext as IWspsActionCtx).regs = undefined;
			if (!selectedWsps || selectedWsps.length == 0) {
				(wspsMgr.wspsActionsContext as IWspActionCtx).reg = wspsMgr.reg;
			} else if (selectedWsps.length == 1) {
				(wspsMgr.wspsActionsContext as IWspActionCtx).reg = selectedWsps[0].reg;
			} else {
				(wspsMgr.wspsActionsContext as IWspActionCtx).reg = wspsMgr.reg;
				(wspsMgr.wspsActionsContext as IWspsActionCtx).regs = [];
				selectedWsps.forEach((wsp) => {
					(wspsMgr.wspsActionsContext as IWspsActionCtx).regs.push(wsp.reg);
				});
			}
			wspsMgr.wspsToolbar.refreshContent();
		});

		this.grid.uiEvents.on('rowDblclick', (row: GridDataRowJson<JWspInfoInList> | null, ev: MouseEvent) => {
			OpenPropsWspAction.SINGLETON.executeIfAvailable((this.wspsActionsContext as IWspActionCtx), ev);
		});

		this.grid.setAttribute("c-resizable", "");
		sr.appendChild(this.grid);

		this.dataHolder.connectToGrid(this.grid);

		return this.refresh();
	}

	/**
	 * Refresh UI
	 * Gestion de la réentrance
	 */
	async _refresh() {
		this.refreshFreeze(1);
		try {
			super._refresh();
			const selectedWsps = this.getSelectedWsps();
			this.filterInput.value = "";
			this.fetchState = "pending";
			this._lastListWsps = await this.listWsps();
			this._lastListWsps.sort((w1, w2) => (w1.title || w1.wspCd).localeCompare(w2.title || w2.wspCd));
			this.fetchState = "done";
			// On ne suit QUE les ateliers loadés (besoin : maj des colonnes qui affichent le statut)
			this._lastListWsps.forEach((wspInfos) => {
				if (wspInfos.status !== EWspLoadingStatus.notLoaded && !(this.reg.env as IWspsLivePlacePointer).place.isConcernedByWsp(wspInfos.wspCd))
					(this.reg.env as IWspsLivePlacePointer).place.registerWsp(this.getRowDatasWsp(wspInfos));
			});

			this.dataHolder.setDatas(this._lastListWsps);
			if (selectedWsps)
				this.selectByWsp(selectedWsps);
			else
				this.grid.setSelectedRows(0);
			this.grid.focus();
		} catch (e) {
			this.fetchState = "failed";
			this._lastListWsps = [];
			this.dataHolder.setDatas([]);
			throw e;
		} finally {
			this.refreshFreeze(-1);
		}
	}

	/** Retourne l'objet WSP associé à une row */
	protected getRowDatasWsp(rowData: JWspInfoInList & IWspPointer) {
		if (!rowData.wsp)
			rowData.wsp = new Wsp(this.reg.env.universe.wspServer, rowData.wspCd, rowData as JInfoWsp);
		return rowData.wsp;
	}

	/** Retourne la liste des ateliers à afficher */
	abstract listWsps(): Promise<(JWspInfoInList & IWspPointer)[]>

	/**
	 * Tree : affichage explicite des lignes en erreur
	 * @param row
	 * @param line
	 */
	redrawLine(row: GridDataRowJson<JWspInfoInList & IWspPointer>, line: HTMLElement): void {
		DOM.setAttrBool(line, "data-removed", row.rowDatas.status==EWspLoadingStatus.noWsp);
	}

	/**
	 * Tree : fonction retournant le texte à afficher en cas de tree vide
	 */
	protected emptyBody() {
		switch (this.fetchState) {
		case "done":
			return <c-msg>Aucun atelier</c-msg>;
		case "failed":
			return <c-msg level="error">Échec au chargement</c-msg>;
		default:
			return <c-msg>Chargement en cours...</c-msg>;
		}
	}

	/**
	 * Filter
	 * @param ev
	 */
	protected onFilterInput(this: HTMLInputElement, ev: Event) {
		const me = DOMSH.findHost<WspsListMgr<any>>(this);
		const selectedWsps = me.getSelectedWsps();
		const text = this.value;
		if (text) {
			const pattern = new RegExp(LANG.escape4RegexpFuzzy(text), 'i');
			const fullArr = me.dataHolder.getDatas();
			const arr = [] as JWspInfoInList[];
			for (let i = 0, s = fullArr.length; i < s; i++) {
				const v = fullArr[i];
				if (pattern.test(v.title)) arr.push(v);
			}
			me.dataHolder.setDatas(arr);
		} else {
			me.dataHolder.setDatas(me._lastListWsps);
		}
		if (selectedWsps)
			me.selectByWsp(selectedWsps);
	}

	/**
	 * Sélection des entrées de l'arbre via une liste de JWspInfoInList sur un tree **chargé**
	 * Si un itemType demandé n'est pas présent dans le tree, aucune erreur n'est remontée
	 * @param JWspInfoInList[]
	 */
	selectByWsp(wsps: Wsp[]): void {
		if (wsps != null) {
			const selectedRows: number[] = [];
			for (let i = 0, c = this.dataHolder.countRows(); i < c; i++) {
				const wspRow = this.dataHolder.getRow(i).rowDatas as JWspInfoInList;
				if (wspRow && wsps.find(wsp => {
					if (wsp && wsp.code == wspRow.wspCd)
						return true;
				})) {
					selectedRows.push(i);
				}
			}
			this.grid.setSelectedRows(selectedRows);
		}
	}

	getSelectedWsps(): Wsp[] {
		const wsps: Wsp[] = [];
		if (this.dataHolder) {
			this.dataHolder.getSelectedDatas().forEach((entry) => {
				wsps.push(this.getRowDatasWsp(entry));
			});
		}
		return wsps;
	}

	onViewShown() {
		if (this.initialized) this.refresh();
	}

	onViewBeforeHide(close?: boolean): boolean {
		return true;
	}

	onViewHidden(closed?: boolean): void {
		if (this.initialized) {
			if (closed)
				(this.reg.env as IWspsLivePlacePointer).place.closePlace();
		} else {
			this.initializedAsync.then(() => this.onViewHidden());
		}
	}

	protected _onWspLiveStateChange: () => void;

	async onWspLiveStateChange(wsp: Wsp) {
		let datas = this.dataHolder.getDatas();
		let wspEntry = datas.find((entry) => entry.wspCd === wsp.code);
		if (wspEntry) {
			const offset = this.dataHolder.getOffset(wspEntry);
			let infoWsp = wsp.infoWsp || wsp.infoWspError;
			if (wsp.isDeleted) {
				this.dataHolder.updateDatasInTree(null, offset, 1);
			} else if (infoWsp) {
				Object.assign(wspEntry, infoWsp);
				this.grid.invalidateRows(offset, 1);
			}
		}
	}


}

