/** Grid des ateliers lives */
export class AliveWspsMgr<E extends IRegPointer<IChainEnv>> extends WspsListMgr<E> {
	protected async _initialize(init: OWspsListMgrInit): Promise<void> {
		await super._initialize(init);
		this._initAndInstallSkin("wsps-alive-wspsmgr", init);

		/** Init les listes du registre **/
		/** Actions : contexte menu ET toolbar **/
		this.reg.addToList("actions:wsps:wspsmgr", "actionCreateWsp", 1, new ActionWrapperRefreshWspMgr(this).setOverridenSvc(CreateWspAction.SINGLETON), 1);
		this.reg.addToList("actions:wsps:wspsmgr", "actionCreateWspFromWsp", 1, new ActionWrapperRefreshWspMgr(this).setOverridenSvc(CreateWspFromWspAction.SINGLETON), 1);
		this.reg.addToList("actions:wsps:wspsmgr", "actionDeleteWsp", 1, new ActionWrapperOnWspsChanges(this).setOverridenSvc(new ActionWrapper()
			.setExecute(async function (this: ActionWrapper<any>, ctx: IWspActionCtx, ev?) {
				// Fermeture de l'atelier éventuellement ouvert dans le desk
				const mainApp = (desk as AppFrameDeskFeat).getMainApp();
				if ((mainApp as IApp<IWspEnv>)?.reg?.env.wsp?.equals(ctx.reg.env.wsp))
					if (AppFrameDeskFeat.isIn(desk)) await desk.gotoHome();
				return this.getWrapped(ctx).execute(ctx, ev);
			})
			.setOverridenSvc(DeleteWspAction.SINGLETON)
		));
		this.reg.addToList("actions:wsps:wspsmgr", "actionDeleteWsps", 1, new ActionWrapperOnWspsChanges(this).setOverridenSvc(new ActionWrapper()
			.setExecute(async function (this: ActionWrapper<any>, ctx: IWspsActionCtx, ev?) {
				// Fermeture de l'atelier éventuellement ouvert dans le desk
				const mainApp = (desk as AppFrameDeskFeat).getMainApp();
				if (mainApp && ctx.regs?.length) {
					for (const reg of ctx.regs) {
						if ((mainApp as IApp<IWspEnv>)?.reg?.env.wsp.equals(reg.env.wsp)) {
							if (AppFrameDeskFeat.isIn(desk)) await desk.gotoHome();
							break;
						}
					}
				}
				return this.getWrapped(ctx).execute(ctx, ev);
			})
			.setOverridenSvc(DeleteWspsAction.SINGLETON)
		));
		this.reg.addToList("actions:wsps:wspsmgr", "actionOpenPropsWsp", 1, new ActionWrapperOnWspsChanges(this).setOverridenSvc(OpenPropsWspAction.SINGLETON));
		this.reg.addToList("actions:wsps:wspsmgr", "actionOpenPermsWsp", 1, new ActionWrapperOnWspsChanges(this).setOverridenSvc(OpenPermsWspAction.SINGLETON));
		this.reg.addToList("actions:wsps:wspsmgr", "actionPropsWspsModel", 1, new ActionWrapperOnWspsChanges(this).setOverridenSvc(OpenPropsWspsAction.SINGLETON));

		/** Actions : toolbar
		 * 		liste actions:wsps:wspsmgr:toolbar uniquement
		 */
		this.reg.addToList("actions:wsps:wspsmgr:toolbar", "actionRefreshUi", 1, actionRefreshUi);
		this.reg.addToList("actions:wsps:wspsmgr:toolbar", "actionImportWsp", 1, new ActionWrapperRefreshWspMgr(this).setOverridenSvc(ImportWspAction.SINGLETON), 2);

		/** Actions : ctxtmenu
		 * 		liste actions:wsps:wspsmgr:ctxtmenu uniquement
		 */
		const wspsMgr = this;
		this.reg.addToList("actions:wsps:wspsmgr:ctxtmenu", "actionCreateDrfWsp", 1, new ActionWrapperRefreshWspMgr(this).setOverridenSvc(CreateDrfWspAction.SINGLETON));
		this.reg.addToList("actions:wsps:wspsmgr:ctxtmenu", "actionCreateDrvWsp", 1, new ActionWrapperRefreshWspMgr(this).setOverridenSvc(CreateDrvWspAction.SINGLETON));
		this.reg.addToList("actions:wsps:wspsmgr:ctxtmenu", "actionOpenWsp", 1, new ActionWrapper()
			.setExecute(function (this: ActionWrapper<any>, ctx: IWspActionCtx, ev?) {
				const popup = POPUP.findPopupableParent(wspsMgr);
				if (popup) popup.close();
				return this.getWrapped(ctx).execute(ctx, ev);
			})
			.setOverridenSvc(new OpenWsp(WSP.defaultWspOpenMode(this.reg)).setLabel("Ouvrir l'atelier..."))
		);

		let columnDefs = [];
		columnDefs.push(
			new GridColDef('title').setLabel("Titre")
				.setDefaultSort(1, 'ascendant')
				.setFlex('25em', 1, 1).setMinWidth('8em').setSortable(true)
				.setCellBuilder(new CellBuilderString("title").override("_getValue", (row: GridDataRowJson<JWspInfoInList>): any => {
					if (row.rowDatas.title) return row.rowDatas.title;
					let wsp = this.getRowDatasWsp(row.rowDatas);
					return wsp.wspTitle || "[Non titré]";
				})),
			new GridColDef('wspCd').setLabel("Modèle")
				.setFlex('15em', 1, 1).setMinWidth('6em').setSortable(true)
				.setCellBuilder(new CellBuilderWspModel("wspType"))
		);
		if (this.reg.env.universe.adminUsers?.hasAspect(EUserAspects.rolable)) {
			columnDefs.push(new GridColDef('wspSetPerms').setLabel("Permissions affectées").setDescription("Permissions affectées sur l'atelier")
				.setFlex('10em', 1, 1).setMinWidth('6em').setSortable(true)
				.setCellBuilder(new CellBuilderWspSpecifiedRoles()));
		}
		columnDefs.push(new GridColDef('status').setLabel("Statut")
			.setFlex('5em', 1, 1).setMinWidth('4em').setSortable(true)
			.setCellBuilder(new CellBuilderEnum('status', new Map([[EWspLoadingStatus.notLoaded, ""], [EWspLoadingStatus.loading, "Chargement en cours..."], [EWspLoadingStatus.noWsp, "Supprimé"], [EWspLoadingStatus.ok, "Opérationnel"], [EWspLoadingStatus.failed, "Non accessible"]]))
				.setCellClass("center")
				.setDescriptionFunc((row: GridDataRowJson<JWspInfoInList>) => {
					const key = row.getData("status") || "";
					return key === EWspLoadingStatus.notLoaded ? "Information non disponible" : "";
				})));

		if (!init.wspsGrid) init.wspsGrid = [] as OWspsListGridInit;
		if (init.wspsGrid.columnDefs === undefined) init.wspsGrid.columnDefs = columnDefs;

		return this.initUi(init);
	}

	async listWsps(): Promise<JWspInfoInList[]> {
		return (await WSP.listWsps(this.reg.env.universe.wspServer, {fields: ["srcRoles", "srcRi"], withWspSrcSpecifiedRoles: true})).wsps;
	}

}

customElements.define("wsps-alive-wspsmgr", AliveWspsMgr);
