		if (ctx.regs) {
			ctx.regs.forEach((entry) => {
				if (entry.env.wsp && !(this.wspsMgr.reg.env as IWspsLivePlacePointer).place.getWsp(entry.env.wsp.code)) (this.wspsMgr.reg.env as IWspsLivePlacePointer).place.registerWsp(entry.env.wsp);
			});
		} else if (ctx.reg?.env.wsp && !(this.wspsMgr.reg.env as IWspsLivePlacePointer).place.getWsp(ctx.reg.env.wsp.code))
			(this.wspsMgr.reg.env as IWspsLivePlacePointer).place.registerWsp(ctx.reg.env.wsp);

		await super.execute(ctx, ev);
